import React, { useState, useSearchParams } from 'react';
import classNames from 'classnames';
import { SectionProps } from '../../utils/SectionProps';
import ButtonGroup from '../elements/ButtonGroup';
import Button from '../elements/Button';
import Image from '../elements/Image';
import Modal from '../elements/Modal';



import Checkout from "./Checkout";

const propTypes = {
  ...SectionProps.types
}

const defaultProps = {
  ...SectionProps.defaults
}

const HeroCancel = ({
  className,
  topOuterDivider,
  bottomOuterDivider,
  topDivider,
  bottomDivider,
  hasBgColor,
  invertColor,
  ...props
}) => {

  const [videoModalActive, setVideomodalactive] = useState(false);
  const [greetingText, setGreetingText] = useState("");

  const openModal = (e) => {
    e.preventDefault();
    setVideomodalactive(true);
  }

  const closeModal = (e) => {
    e.preventDefault();
    setVideomodalactive(false);
  }   

  const outerClasses = classNames(
    'hero section center-content',
    topOuterDivider && 'has-top-divider',
    bottomOuterDivider && 'has-bottom-divider',
    hasBgColor && 'has-bg-color',
    invertColor && 'invert-color',
    className
  );

  const innerClasses = classNames(
    'hero-inner section-inner',
    topDivider && 'has-top-divider',
    bottomDivider && 'has-bottom-divider'
  );

  const onChangeHandler = event => {
    setGreetingText(event.target.value);
 };

 const [isLoading, setLoading] = useState(false);

  let search = window.location.search;
  let params = new URLSearchParams(search);
  let greeting = greetingText;

  if(params.get('greeting') !== null) {
    greeting = params.get('greeting');
  }

 
 
  return (
    <section
      {...props}
      className={outerClasses}
    >
      <div className="container-sm">
        <div className={innerClasses}>
          <div className="hero-content">
            <h1 className="mt-0 mb-16 reveal-from-bottom" data-reveal-delay="200">
              Don't lose your <span className="text-invertColor">Sand Greeting</span>
            </h1>
          </div>

          <div className="hero-input"> 
          <form onSubmit={e => { e.preventDefault(); return false; }}>
            <input className='greeting' type="text" id="greeting" placeholder="Enter your greeting" value={greeting}  onChange={onChangeHandler} />
                <Checkout greeting={greeting} />
          </form>
          </div>

          <div className="hero-figure reveal-from-bottom illustration-element-01" data-reveal-value="20px" data-reveal-delay="800">
          
              <img
                id="video-image"
                className="has-shadow"
                src={require('./../../assets/images/YourMessage.jpg')}
                alt="You message requires a custom sand greeting to be created."
                disabled={isLoading}
                width={896}
                height={504} />
           

        
             
          </div>
       
        </div>
      </div>
    </section>
  );
}

HeroCancel.propTypes = propTypes;
HeroCancel.defaultProps = defaultProps;

export default HeroCancel;